// Refactored CSS for spacings between form elements

.uol-form__input-label__text {
  margin-bottom: $spacing-3;
}
.uol-form__input-label__hint {
  margin-top: calc(#{$spacing-2} * -1); // -8px;
  margin-bottom: $spacing-3;
}
.uol-form__input__error {
  margin-bottom: $spacing-2;
}

/*
Margin below form error message
Scope to just campaign landing pages so as not to affect other forms
*/
.uol-clp .uol-form__form-error {
  margin-bottom: $spacing-6;

  @include media(">=uol-media-l") {
    margin-bottom: $spacing-7;
  }
}
