.uol-index-table-caption {
  @extend %text-size-heading-4;
  
  color: $color-font--dark;
  margin: $spacing-4 0 0;
  padding: $spacing-4;

  @include media("<uol-media-m") {
    border: 1px solid $color-border;
    border-bottom: none;
  }
}

.uol-index-table--results-container {
  padding: $spacing-4;

  @include media(">uol-media-m") {
    text-align: right;
  }
}

.uol-index-table--results {
  @include font-size-responsive(1rem);
}

.uol-index-table {
  @extend %text-size-paragraph--small;

  margin-bottom: $spacing-4;
  border: 1px solid $color-border;
  border-bottom: none;
  font-variant-numeric: lining-nums;

  @include media(">=uol-media-m") {
    margin: $spacing-4;
    position: relative;
  }

  .uol-index-table__th {
    border-top: 1px solid $color-border;
    border-bottom: 1px solid $color-border;
  }

  thead {

    @include media(">=uol-media-m") {
      z-index: 2;
    }
  }

  th,
  td {
    vertical-align: top;

    @include media(">=uol-media-m") {
      padding: $spacing-4;
    }
  }

  th {
    font-size: 20px !important;
    font-weight:  $font-weight-bold--sans-serif;

    @include media(">=uol-media-m") { 
      text-align: left;
      position: sticky;
      top: 0;
      padding-bottom: 20px;
      background: $color-white;
      z-index: 2;

      .csspositionsticky & {
        &::before {
          content: "";
          display: block;
          position: absolute;
          top: 0;
          bottom: -4px;
          left: 0;
          right: 0;
          background: linear-gradient(0deg, rgba(#000, 0) 0, rgba(#000, 0.4) 4px, rgba(#000, 0) 4px);
          opacity: 0;
          transition: opacity 0.7s ease;
        }
      }

      &.uol-index-table__th--numeric {
        text-align: right;
      }

      &.uol-index-table__th--image-column {
        @include media(">=uol-media-m") {
          padding-left: 6.5rem;
        }
      }
    }
  }

  .thead--stuck th {
    @include media(">=uol-media-m") {
      &::before {
        opacity: 1;
      }
    }
  }

  td {
    color: $color-font;

    a {
      color: $color-font;
      text-decoration: underline;

      &:hover,
      &:focus {
        text-decoration-color: $color-brand;
      }
    }
  }

  tbody {
    line-height: 1.5;

    @include media(">=uol-media-m") {
      z-index: 1;
    }

    tr {
      transition: all 0.3s ease;

      @include media(">=uol-media-m") {
        &:nth-child(odd) {
          background: $color-grey--faded;
        }
      }
    }

    td {
     border-bottom: 1px solid $color-border; 
    }
  }

  .uol-index-table__td--type-numeric {
    @include media(">=uol-media-m") {
      text-align: right;
    }
  }

  .uol-index-table__td--type-img {
    position: relative;
    margin-top: -#{$spacing-4};
    margin-left: -#{$spacing-4};
    margin-right: -#{$spacing-4};
    padding: $spacing-4 $spacing-4 $spacing-4 6.5rem;

    &::before {
      content: "";
      float: left;
      overflow: hidden;
      height: $spacing-8;
      font-size: 0;
   }
  }

  .uol-index-table__td__img {
    position: absolute;
    top: $spacing-4;
    left: $spacing-4;
    display: block;
    width: $spacing-9;
    height: $spacing-9;
    background-color: $color-white;
    border-radius: 50%;
    overflow: hidden;

    @include media(">=uol-media-m") {
      width: 64px;
      height: 64px;
      top: 16px;
    }

    &::before,
    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: inherit;
      height: inherit;
      border-radius: inherit;
    }

    &::before {
      background: 50% / cover;
      background-image: inherit;
    }

    &::after {
      box-shadow: inset 0 2px 5px rgba($color-black, 0.16);
    }
  }

  .uol-index-table__td__img--profile {
    background: $color-white url("../img/avatar-missing.png");
  }

  .uol-index-table__sort-select {
    display: block;
    font-weight: $font-weight-bold--sans-serif;
    width: 100%;
    margin: 0;
    padding: $spacing-4;
    border: none;
    border-top: 1px solid $color-border;
    border-bottom: none; 

    @supports (-webkit-appearance:none) {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      background: url("../img/sort.svg") no-repeat right $spacing-4 top 50%;
    }

    @media (-ms-high-contrast: active) {
      -webkit-appearance: menulist;
      -moz-appearance: menulist;
      appearance: menulist;
      background: initial;
    }

    @include media(">=uol-media-m") {
      display: none;
    }
  }

  .uol-index-table__th-inner {
    display: flex;
  }

  .uol-index-table__btn-sort {
    position: relative;
    width: 25px;
    height: 24px;
    background: transparent;
    border: none;

    @include media("<uol-media-m") {
      display: none;
    }

    &::before,
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 0;
      height: 0;
      left: 8px;
      border: 0.35rem solid transparent;
      border-bottom: none;
      border-top: 0.4rem solid $color-black--dark;

      @media (forced-colors: active) {
        border: 0.35rem solid canvas;
        border-top: 0.4rem solid $color-black--dark;
      }

      @media (-ms-high-contrast: active) {
        border: 0.35rem solid window;
        border-top: 0.4rem solid windowText;
      }
    }

    &::before {
      transform: rotate(180deg) translateY(120%);
    }

    &::after {
      transform: translateY(20%);
    }

    &.sorted--asc {

      &::after {
        opacity: 0;
      }
    }

    &.sorted--desc {

      &::before {
        opacity: 0;
      }
    }
  }
}

// uol-index-table--stacked class added with javascript
.uol-index-table--stacked {
  margin: 0 0 $spacing-4;

  @include media("<uol-media-m") {
    display: block;
    border-top: none;

    thead {
      position: absolute !important;
      clip: rect(1px, 1px, 1px, 1px);
      height: 1px;
      width: 1px;
      overflow: hidden;
      left: -9999px;
    }

    tbody {
      display: block;

      tr {
        display: block;
        margin-bottom: 0;

        &:last-of-type {
          
          td:last-of-type {
            border-bottom: 1px solid $color-border;
          }
        }
      }

      td {
        display: block;
        padding: $spacing-4;
        color: $color-font--dark;
        border-top: 1px solid $color-border;
        border-bottom: none;

        &:first-of-type {
          background: $color-grey--faded;
        } 
      }

      .uol-index-table__td--type-img {
        position: relative;
        background: $color-cream--dark;
        padding: $spacing-5 $spacing-4 $spacing-5 7rem;
        margin: 0;
        min-height: 64px;
      }

      .uol-index-table__td__img {
        position: absolute;
        top: $spacing-4;
        left: $spacing-4;
        display: block;
        width: $spacing-9;
        height: $spacing-9;
        background-color: $color-white;
        border-radius: 50%;
        overflow: hidden;

        &::before,
        &::after {
          content: "";
          display: block;
          position: absolute;
          top: 0;
          right: 0;
          width: inherit;
          height: inherit;
          border-radius: inherit;
        }

        &::before {
          background: 50% / cover;
          background-image: inherit;
        }

        &::after {
          box-shadow: inset 0 2px 5px rgba($color-black, 0.16);
        }

        img {
          width: 100%;
          height: auto;
        }
      }
    }
  }
}

.uol-index-table__pseudo-th {
  display: block;
  font-size: 18px !important;
  font-weight: $font-weight-bold--sans-serif;
  color: $color-font;
  margin-bottom: $spacing-1;

  @include media(">=uol-media-m") {
    display: none;
  }
}
